import { useEffect, useState } from "react";
import { Getprofile, cloudImage, countryGet, languageGet, updateProfilee } from "../../../api/login/Login";
import { toast } from "react-toastify";
import { FaCamera } from "react-icons/fa";
import { BiCamera } from "react-icons/bi";
import { baseUrlImage } from "../../../baseUrl";
import { Avatar, Box, Card, CardContent, Grid, Typography, Tabs, Tab, Stack } from "@mui/material";
import { Email, Phone } from "@mui/icons-material";
import { Person, Business, VideoCall, Gavel, CheckCircle } from "@mui/icons-material";



const DetailsSection = ({ title, children }) => {
  return (
    <Box
      sx={{
        mt: 3,
        bgcolor: "white",
        boxShadow: `rgba(99, 99, 99, 0.2) 0px 2px 8px 0px`,
        borderRadius: "8px",
        width: "100%",
        //   mb: 3,
        pb: 3,
      }}
    >
      <Box borderBottom={1} borderColor={"#F4F0F0"}>
        <Typography
          variant="h6"
          sx={{ px: 3, py: 2, fontWeight: "600", fontSize: "18px" }}
        >
          {title}
        </Typography>
      </Box>
      {children}
    </Box>
  );
};

const DetailRow = ({ label, value }) => {
  return (
    <Stack borderBottom={1} borderColor={"#F4F0F0"} direction={"row"} width={"100%"}>
      <Typography variant="h6" sx={{ px: 3, py: 2, flex: 1 }} style={{ fontSize: '14px', color: 'var(--bs-primar2)' }}>{label} :</Typography>
      <Typography variant="h6" sx={{ px: 3, py: 2, flex: 1 }} style={{ fontSize: '14px', color: 'var(--bs-primar2)', opacity: '0.8' }}>{value}</Typography>
    </Stack>
  );
};


function Profile() {
  const [loader1, setloader1] = useState(false)
  const [country, setCountry] = useState()
  const [language, setLanguage] = useState()
  const [profileImage, setProfileImage] = useState()
  console.log(profileImage);
  const [data, setData] = useState()
  // console.log(data?.refer_id);

  const [isModalOpen, setModalOpen] = useState(false);

  const toggleModal = () => {
    console.log("Click")
    setModalOpen(!isModalOpen);
  };

  const [initialValues, setInitialValues] = useState({
    name: "",
    email: "",
    mobile: '',
    language_id: '',
    profile: '',
    country: '',
    user_id: '',
    alternate_mobileNo: '',
    state: '',
    country: '',
    presentAddr: '',
    educationQualification: '',
    pinCode: '',
    locking_amt: ''
  });

  const handleChange = (e) => {
    const clone = { ...initialValues }
    const value = e.target.value
    const name = e.target.name
    clone[name] = value
    setInitialValues(clone)

  }

  const getCountry = async () => {
    const res = await countryGet()
    setCountry(res?.data?.data)
  }
  const getlaguage = async () => {
    const res = await languageGet()
    setLanguage(res?.data?.data)

  }


  const imgs = new FormData();
  const [errorMessage, setErrorMessage] = useState('');
  console.log(errorMessage);
  const [imgss, setImgss] = useState(data?.profile)
  // console.log(imgss);
  const colodinaryImage = async (e) => {
    setProfileImage(e.target.files[0])
    imgs.append("image", e.target.files[0]);
    const allowedTypes = [
      'video/mp4',
      'application/msword',
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document',
      'application/vnd.ms-excel',
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      'application/pdf',
      'application/x-rar-compressed',
      'application/x-zip-compressed',
      'application/zip'
    ];
    // console.log(e.target.files[0].type);
    if (e.target.files[0] && allowedTypes.includes(e.target.files[0].type)) {
      // setProfileImage(e.target.files[0])
      setErrorMessage('Invalid file type. Please select a valid file.');
    } else {
      setProfileImage(e.target.files[0])
      setErrorMessage('');
      try {
        const res = await cloudImage(imgs)


        setTimeout(() => {
          setImgss(res?.data?.data?.url)
          if (res.data.error == false) {
            updateProfile(res?.data?.data?.url)
          }
        }, 1000);
      } catch (error) {

      }
    }

  }
  console.log(imgss);

  const toastSuccessMessage = (message) => {
    toast.success(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };
  const toastSuccessMessage2 = (message) => {
    toast.error(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };


  const updateProfile = async (url) => {
    // console.log('hjkhk', url);
    setloader1(true)
    const clone = { ...initialValues, profile: url ? url : imgss, user_id: window.localStorage.getItem('userIdToken') }
    // console.log(clone);
    try {
      const res = await updateProfilee(clone)
      if (res?.status == '200') {
        toastSuccessMessage(res.data.message)
      }
      if (res?.data?.error == true) {
        toastSuccessMessage2(res.data.message)
      }
      // console.log(res);
      setloader1(false)
    } catch (error) {
      setloader1(false)
    }
  }

  const getDataProfile = async () => {
    try {
      const res = await Getprofile()
      setData(res?.data?.data)

    } catch (error) {

    }
  }

  useEffect(() => {
    getCountry()
    getlaguage()
    getDataProfile()
  }, [])

  useEffect(() => {
    const ob = {
      name: data?.name,
      email: data?.email,
      mobile: data?.mobile,
      language_id: data?.language_id,
      country: data?.country,
      profile: data?.profile,
      state: data?.state,
      presentAddr: data?.presentAddr,
      educationQualification: data?.educationQualification,
      pinCode: data?.pinCode,
      locking_amt: data?.locking_amt
    }
    setInitialValues(ob)
    setImgss(data?.profile)
    // console.log(data?.profile);
  }, [data])

  const [tabValue, setTabValue] = useState(0);
  return (
    <>
      <Box sx={{ p: 3, pb: 0, bgcolor: "white", boxShadow: `rgba(99, 99, 99, 0.2) 0px 2px 8px 0px`, borderRadius: '8px', width: '100%' }}>
        <Grid container padding={2}>
          <Grid item xs={12} md={6} borderRight={1} borderColor={'#F4F0F0'}>
            <Stack direction="row" spacing={4} marginTop={2} alignItems="center">

              {/* <Avatar
              src={`${baseUrlImage}${imgss}`} // Replace with actual image path
              sx={{ width: 80, height: 80 }}
              onClick={toggleModal}
            /> */}
              <Stack direction="row" spacing={4} marginTop={2} alignItems="center" position="relative">
                <Avatar
                  src={`${baseUrlImage}${imgss}`}
                  sx={{ width: 80, height: 80, cursor: "pointer" }}
                  onClick={toggleModal}
                />
                <label htmlFor="profile-image-upload" style={{ position: "absolute", bottom: -10, left: 25, cursor: "pointer", background: "#fff", borderRadius: "50%", padding: "5px", boxShadow: "0px 2px 4px rgba(0,0,0,0.2)" }}>
                  <FaCamera size={20} color="var(--bs-primary2)" />
                </label>
                <input
                  type="file"
                  id="profile-image-upload"
                  accept="image/*"
                  style={{ display: "none" }}
                  onChange={colodinaryImage}
                />
              </Stack>
              <div>


                <Typography variant="h6" style={{ fontSize: '16px', color:"var(--bs-primary2)" }}>{initialValues.name}</Typography>
                <Typography variant="body2" style={{ fontSize: '14px', color:"var(--bs-primary2)"}} >
                  Code - {data?.refer_id}
                </Typography>
              </div>
            </Stack>
          </Grid>
          <Grid item xs={12} md={6}>
            <div className="ms-lg-4 pt-4 pt-lg-0">
              <Typography variant="h6" style={{ fontSize: '16px', color:"var(--bs-primary2)" }} className="fs-bold mb-4">Contact Details</Typography>
              <Typography display="flex" style={{ fontSize: '14px', color:"var(--bs-primary2)", opacity: '0.8' }} alignItems="center" gutterBottom>
                <Email fontSize="small" sx={{ mr: 1 }} /> {initialValues.email}
              </Typography>
              <Typography display="flex" style={{ fontSize: '14px', color: 'var(--bs-primar2)', opacity: '0.8' }} alignItems="center">
                <Phone fontSize="small" sx={{ mr: 1 }} /> {initialValues.mobile}
              </Typography>
            </div>
          </Grid>
        </Grid>
        <Tabs
          value={tabValue}
          onChange={(e, newValue) => setTabValue(newValue)}
          sx={{
            mt: 2,
            "& .MuiTab-root": {
              color: 'var(--bs-primar2)',
              flexDirection: "row",
              //   padding: "12px 24px",
              minWidth: "auto"
            },
            "& .Mui-selected": {
              color: "var(--bs-primar2) !important",
              //   fontWeight: "bold",
              borderBottom: "2px solid var(--bs-primary1)"
            },
            "& .MuiTabs-indicator": {
              display: "none"
            }
          }}
          variant="scrollable"
          scrollButtons="auto"
        >
          <Tab icon={<Person sx={{ mr: 1 }} />} label="Personal" iconPosition="start" />
          <Tab icon={<Business sx={{ mr: 1 }} />} label="Business" iconPosition="start" />
          {/* <Tab icon={<VideoCall sx={{ mr: 1 }} />} label="Video/KYC" iconPosition="start" /> */}
          {/* <Tab icon={<Gavel sx={{ mr: 1 }} />} label="Agreements" iconPosition="start" /> */}
          {/* <Tab icon={<CheckCircle sx={{ mr: 1 }} />} label="Whitelisted Accounts" iconPosition="start" /> */}
        </Tabs>
      </Box>
      <Grid container spacing={2}>
        {/* User Details */}
        {tabValue === 0 &&
          <>

            <Grid item xs={12} sm={6}>
              <DetailsSection title="User Details">
                <DetailRow label="Locked Amount" value={initialValues.locking_amt} />
                <DetailRow label="Education Qualification" value={initialValues.educationQualification} />
                <DetailRow label="Present Address" value={initialValues.presentAddr} />
                <DetailRow label="State" value={initialValues.state} />
                <DetailRow label="Pin Code" value={initialValues.pinCode} />
              </DetailsSection>
            </Grid>

            {/* Aadhar Details */}
            <Grid item xs={12} sm={6}>
              <DetailsSection title="Aadhaar Details">
                <DetailRow label="Aadhaar Number" value={data?.adhaar_number} />
                <DetailRow label="Name" value={data?.adhaar_name} />
                <DetailRow label="Date of Birth" value={initialValues.dob} />
                <DetailRow label="District" value={initialValues.district} />
                <DetailRow label="State" value={initialValues.state} />
                <DetailRow label="Country" value={initialValues.country} />
              </DetailsSection>
            </Grid>

            <Grid item xs={12} sm={6}>
              <DetailsSection title="Bank Details">
                <DetailRow label="Account Number" value={data?.bankDetails?.bank_account_number} />
                <DetailRow label="IFSC code" value={data?.bankDetails?.bank_ifsc} />
                <DetailRow label="Bank Name" value={data?.bankDetails?.bank_name} />
                <DetailRow label="Verified Name" value={data?.bankDetails?.bank_account_name} />
              </DetailsSection>


            </Grid>
            <Grid item xs={12} sm={6}>
              <DetailsSection title="Pancard Details">
                <DetailRow label="Pancard Name" value={data?.pan_name} />
                <DetailRow label="Pancard Number" value={data?.pan_number} />
              </DetailsSection>
            </Grid>
          </>
        }
        {tabValue === 1 &&
          <>

            <Grid item xs={12} sm={6}>
              {data?.isGstAvailable ?
                <DetailsSection title="GST Details">
                  <DetailRow label="GST Number" value={data?.businessInfo?.gst_NUmber} />
                  <DetailRow label="Legal Name of Business" value={data?.businessInfo?.legal_name_of_business} />
                  <DetailRow label="Constitution of Business" value={data?.businessInfo?.constitution_of_business} />
                  <DetailRow label="Taxpayer Type" value={data?.businessInfo?.taxpayer_type} />
                  <DetailRow label="Principal Place Address" value={data?.businessInfo?.principal_place_address} />
                  <DetailRow label="Business Pincode" value={data?.business_pincode} />
                </DetailsSection>
                :
                <DetailsSection title="Business Details">
                  <DetailRow label="Shop Name" value={data?.shop_name} />
                  <DetailRow label="Business Address" value={data?.business_Address} />
                  <DetailRow label="Business Area" value={data?.business_Area} />
                  <DetailRow label="Business City" value={data?.business_city} />
                  <DetailRow label="Business State" value={data?.business_state} />
                  <DetailRow label="Business Pincode" value={data?.business_pincode} />
                </DetailsSection>}
            </Grid>
          </>
        }

      </Grid>


      {isModalOpen && (
        <div className="modall" onClick={toggleModal}>
          <span className="close">&times;</span>
          <img className="modal-content" src={`${baseUrlImage}${imgss}`} alt="Profile Large" />
        </div>
      )}
    </>
  )
}
export default Profile